<template>
<div>
	<div class="searchTreeForm w3-display-middle" v-if="searchState == 'form_searching'" >
      <label>Searching</label>
      <i class="fa fa-spinner fa-spin" style="font-size:24px"></i>
    </div>

    <div v-if="searchState == 'table'" class="resultBox">
      <button v-on:click="closeResultPage" type="button" class="close" data-dismiss="modal" aria-label="Close" ><span aria-hidden="true">&times;</span></button>
      <p>About  {{Object.keys(treeResult).length}} results</p>
      <table class="table" id="trees_result" >
        <thead>
          <tr>
            <th></th>
            <th>Tree Code</th>
            <th>Name</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in treeResult" v-on:click="showDetail(item)" >
            <td><img :src="imgSrcDomain + item.icon"></td>
            <td class="">{{item.Code}}</td>
            <td>{{ item.Name }}</td>
          </tr>
        </tbody>
      </table>
    </div>
</div>
</template>


<script>

	export default {
		created() {
			this.searchTree ();
		},
		data () {
		  return {
		  	searchState: 'form_searching',
		  	searchTree_post_data: {
		  	  codeType : 'TreeCode',
		  	  code : this.$route.params.treeID,
		  	  ts : '0',
		  	  distID : '0',
		  	  departID: '0',
		  	  tc: '0',
		  	  lang: 'zh-HK',
		  	  brr: 'false'
		  	},
		  	treeResult : null,
		  	imgSrcDomain : "https://www.greening.gov.hk/treeregister/map/library/."
		  }
		},
		methods: {
			inform(){
				console.log('h',H);
				this.$router.push(H+'/info/123');
			},
			closeResultPage : function () {
				this.$router.push(H+'/');
			},
			searchTree (event){
			  this.$http.post('https://hk-tree.lotusfa.com/searchTree.php', 
			    this.searchTree_post_data, 
			    {
			      headers : {
			        'Accept' : 'application/json, text/javascript, */*; q=0.01',
			        'Content-Type' : 'application/json',
			        'X-Requested-With' : 'XMLHttpRequest'
			      }
			    }
			    
			    ).then(response => {
			      this.treeResult = response.body.d;
			      this.searchState = 'table';
			  	}, response => {
				    // error callback
				    console.log("err",response);
				});
			},
			showDetail(item){
			  this.$router.push(H+'/info/' + item.ID);
			},
		}
	}
</script>


