<template>
	<div  class="searchTreeForm w3-display-middle" >
		<label>HK Search Tree</label>
		<div class="input-group">
			<input type="text" v-model="searchTree_post_data.code"  />
			<span class="input-group-btn">
			<button v-on:click="searchTree" >
			  <span class="glyphicon glyphicon-search"></span>
			</button>
			</span>
		</div>
    </div>
</template>


<script>
	export default {
		data () {
		  return {
		  	searchTree_post_data: {
		  	  codeType : 'TreeCode',
		  	  code : '',
		  	  ts : '0',
		  	  distID : '0',
		  	  departID: '0',
		  	  tc: '0',
		  	  lang: 'zh-HK',
		  	  brr: 'false'
		  	},

		  }
		},
		methods: {
			searchTree : function (event){
				this.$router.push(H+'/search/'+this.searchTree_post_data.code);
			},
		}
	}
</script>



